    import {
        mapState,mapMutations
    } from 'vuex';
    export default {
		data() {
			return {
				addressData: {
				    id:'',
				    userId:'',
					ecipient: '',
					telephone: '',
					address: '',
					area: '',
					defaultAddress: ""
				},
                defaultFlag:false,
				commitType:""
			}
		},
		onLoad(option){
			let title = '新增收货地址';
			var that = this
			if(option.type==='edit'){
				title = '编辑收货地址'
				that.commitType = option.type
				this.addressData = JSON.parse(option.data)
				if(this.addressData.defaultAddress = 1){
				    that.defaultFlag = true
				}else {
				    that.defaultFlag = false
				}
				console.log(this.addressData)
			}
			this.manageType = option.type;
			uni.setNavigationBarTitle({
				title
			})
		},
        computed: {
            ...mapState(['userInfo'])
        },
		methods: {
			switchChange(e){
			    console.log(e.detail)
			    if(e.detail.value == true){
                    this.addressData.defaultAddress = 1;
				}else {
			        this.addressData.defaultAddress = 0;
				}
				console.log(this.addressData.defaultAddress)

			},
			
			//地图选择地址
			chooseLocation(){
				uni.chooseLocation({
					success: (data)=> {
						this.addressData.addressName = data.name;
						this.addressData.address = data.name;
					}
				})
			},

            navToAddress(){
                var url = '/pages/address/address';
                uni.navigateTo({
                    url
                })
            },
			//提交
			confirm(){
				let data = this.addressData;
				var that = this
				if(!data.ecipient){
					this.$api.msg('请填写收货人姓名');
					return;
				}
				if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.telephone)){
					this.$api.msg('请输入正确的手机号码');
					return;
				}
				if(!data.address){
					this.$api.msg('请在地图选择所在位置');
					return;
				}
				if(!data.area){
					this.$api.msg('请填写门牌号信息');
					return;
				}
				if(this.commitType == 'edit'){
                    this.$http.post("address/updateUsersAddress",data).then((res) => {
                        console.log("success")
                        console.log(res)
                        if(res.data.status === "success"){
                            this.navToAddress()

                        }else{
                            this.$api.msg(res.data.data.errMsg);
                        }
                    }).catch(error => {
                        console.log(error)
                    }).finally(() => {
                        //console.log("success")
                    })
				}else {
				    data.userId = that.userInfo.id
                    this.$http.post("address/addUsersAddress",data).then((res) => {
                        console.log("success")
                        console.log(res)
                        if(res.data.status === "success"){
                            this.navToAddress()
                        }else{
                            this.$api.msg(res.data.data.errMsg);
                        }
                    }).catch(error => {
                        console.log(error)
                    }).finally(() => {
                        //console.log("success")
                    })
				}

				//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
				// this.$api.prePage().refreshList(data, this.manageType);
				// this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
				// setTimeout(()=>{
				// 	uni.navigateBack()
				// }, 800)
			},
		}
	}
